<template>
  <div class="hello">
   <img src="./1.png" alt="">
   <div class="box">
     17年最大的收获就是我们的婚姻，
     <br>
     在未来的路上我们一起加油。
     <br>我们会越过越好。
     <br>
     小霄霄就是我的福星,爱你么么哒
   </div>
   <div class="btn" @click="showHappy">
     最后抽个小惊喜吧~~
   </div>
   <div class="mark" v-if="isShow">
   <div class="happyBox">
      {{str}}
      <div class="btn" @click="again">换一个({{num}})</div>
   </div>
   </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      happyList:[
        '一个大红包',
        '一场宝宝喜欢的电影',
        '一场宝宝喜欢的电影',
        '一顿大餐',
        '一顿大餐',
        '一场说走就走的旅行',
        '一场说走就走的旅行',
        '一场说走就走的旅行',
        '小灰灰专业按摩一次',
        '嘿嘿，惊喜跑丢了，再试一次吧。。',
      ],
      str:'',
      isShow:false,
      num:10,
    }
  },
  components:{

  },
  methods:{
    showHappy(){
        this.str = this.happyList[parseInt(Math.random()*10)]
        this.isShow = true
    },
    again(){
      if(this.num>0){
      this.num -= 1
      this.showHappy()
      }else{
        this.num = 10
        this.str = '谁让你是小霄霄呢，再送你10次机会吧'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.mark{
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.7);
  z-index: 11;
  top: 0;
  left: 0;
}
.happyBox{
  position: fixed;
  width: 80%;
  height: 160px;
  text-align: center;
  color: #fff;
  top: 40%;
  left: 10%;
  margin-top: -100px;
  border: 1px solid #fff;
  border-radius: 3px;
  background: #abcdef;
  padding-top: 40px;
}
.box{
  width: 100%;
  background: #abcdef;
  color: #fff;
  font-size: 20px;
}
.hello{
  min-height: 100%;
  background: #fff;
  position: relative;
  overflow: hidden;
  text-align: left;
  line-height: 2;
}
.hello img{
  width: 100%;
  height: auto;
}
.btn{
  width: 200px;
  display: block;
  background: #f00;
  color: #fff;
  height: 40px;
  line-height: 40px;
  text-align: center;
  padding: 0 10px;
  margin: 0 auto;
  border-radius: 3px;
  margin-top: 20px;
}
</style>
<style>
  *,body,html{
  margin: 0;
  padding: 0;
}
</style>


